/*
Color
*/
/*font*/
@media (min-width: 576px) {
  body {
    font-size: 4vw; }
    body .queries-sm {
      display: block;
      font-size: 2vw; }
    body .queries-xsm {
      display: none; }
    body header {
      flex-direction: row;
      justify-content: space-between; }
      body header nav {
        width: 100%; }
        body header nav .logo {
          margin-left: 10%; }
      body header button {
        align-self: baseline;
        margin-right: 2%; } }

@media (min-width: 768px) {
  /* sticky nav
       sliders
    /* phone at first section and none footer*/
  body {
    font-size: 2vw; }
    body .queries-md {
      display: block;
      font-size: 2vw; }
    body .queries-xsm,
    body .queries-sm {
      display: none; }
  header nav .logo {
    width: 15vw; }
  main #first-section article {
    top: 25%;
    left: 15%; }
  main #first-section .adresses {
    right: 0px;
    bottom: 4%;
    color: #f9f9f9; }
  main #second-section article {
    margin-left: 60%; }
  main #third-section article {
    margin-left: 2%; }
    main #third-section article h2,
    main #third-section article h3,
    main #third-section article h4 {
      text-align: center; }
    main #third-section article p {
      color: #000000;
      text-shadow: none;
      text-align: left; }
  main #fourth-section {
    flex-wrap: nowrap;
    font-size: 1.5; }
    main #fourth-section section {
      align-self: baseline; }
      main #fourth-section section main .card-description {
        min-height: 17.5vw; }
        main #fourth-section section main .card-description ul {
          min-height: 16.4vw; }
      main #fourth-section section main .find-why {
        min-height: 15vw; }
      main #fourth-section section main div {
        margin-left: 30%; }
  footer .logo {
    margin-left: auto;
    width: 15vw; } }

@media (min-width: 992px) {
  body {
    font-size: 2vw; }
    body .queries-lg {
      display: block; }
    body .queries-xsm,
    body .queries-sm,
    body .queries-md {
      display: none; }
  header {
    height: 8vw;
    position: fixed;
    top: 0;
    z-index: 2;
    width: 100%;
    background-color: #f9f9f9; }
    header nav .logo {
      width: 10vw; } }

@media (min-width: 1200px) {
  body {
    font-size: 1.5vw; }
    body .queries-xsm,
    body .queries-sm,
    body .queries-md,
    body .queries-lg {
      display: none; }
    body .queries-xl {
      display: block; }
  main #fourth-section {
    flex-wrap: nowrap;
    font-size: 1.5; }
    main #fourth-section section {
      align-self: baseline; }
      main #fourth-section section main .find-why {
        min-height: 17.5vw; }
      main #fourth-section section main div {
        margin-left: 30%; } }
